import { Component, useState, useEffect } from 'react'
import {
  View,
  Text,
  SwiperItem,
  Swiper
} from '@tarojs/components'
import Taro from '@tarojs/taro'

import './style.scss'

// 【说明】

// const tabArr = [
//   {
//     id: 0,
//     label: '评论',
//     count: 10,
//     height: 100
//   },
//   {
//     id: 1,
//     label: '点赞',
//     count: 5,
//     height: 50
//   },
//   {
//     id: 2,
//     label: '投诉',
//     count: 3,
//     height: 80
//   }
// ]

export default props => {
	const {
		tabArr,
		children,
		onChange
	} = props

	const [curIdx, setCurIdx] = useState(0)
  const [h, setH] = useState('')

	useEffect(()=>{
		const tab = tabArr[curIdx]
		setH(tab.count*tab.height)
	}, [curIdx, tabArr])

	const tabClick = (idx) => {
		setCurIdx(idx)
		onChange&&onChange(idx)
	}

	const swiperChange = (e) =>{
		const idx = e.detail.current
		setCurIdx(idx)
		onChange&&onChange(idx)
	}

  // 渲染TabItem
	const renderTabItem = ()=> {
		return tabArr.map((ele,idx)=>(
			<View
				className={`tab ${curIdx == idx ? 'on' : ''}`}
				key={idx}
				onClick={()=>tabClick(idx)} >
				<Text className="tab-title">
					<Text>{ele.label}</Text>
					<Text>{ele.count}</Text>
				</Text>
				<Text className="tab-line"></Text>
			</View>
		))
	}

  // 渲染SwiperItem
	const renderSwiperItem = ()=> {
	  return tabArr.map((ele,idx)=>(
	    <SwiperItem key={idx}>
	      { children[idx] }
	    </SwiperItem>
	  ))
	}

	return (
		<View
			className='mySwiper'
			onClick='tabTouch'
			onTouchStart='touchStart'
			onTouchEnd='touchEnd'>

			{/*tabs*/}
			<View className="tabs">
				{ renderTabItem() }
			</View>

			{/*swiper容器*/}
			<Swiper
				className='Swiper'
				current={curIdx}
				onChange={swiperChange}
				style={{height: h+'rpx'}}>
				{ renderSwiperItem() }
			</Swiper>
		</View>
	)
}
