import { useState } from 'react'
import { View,Text, Input, Image } from '@tarojs/components'
import './style.scss'
import img from '@/utils/img'
import { QfLogin } from '@/components'

export default props => {
	let {
		actived,
		commentCount,
		starCount,
		isStar,
		onActived,
		onSubmit,
		onAgree
	} = props

	const [value, setValue] = useState('')
	const submit = ()=> {
		console.log('----', value)
		onSubmit&&onSubmit(value)
		setValue('')
	}

	return (
		<View className='qf-comment'>
			<View style={{position:'relative'}}>
				<QfLogin desc='未登录不能评论' />
				{
					(actived || false)
					? <View className='on'>
							<Input value={value} onInput={e=>setValue(e.detail.value)} />
							<Text onClick={()=>submit()}>发表</Text>
						</View>
					: <View className='off'>
							<Input onFocus={()=>onActived&&onActived(true)} />
							<View className='comm'>
								<Image src={img.comment} onClick={()=>onActived&&onActived(true)} />
								<Text>{commentCount||0}</Text>
							</View>
							<View className='star'>
								<Image
									src={isStar?img.starOn:img.star}
									onClick={()=>onAgree&&onAgree()}
								/>
								<Text style={{color:(isStar?'red':'black')}}>{starCount||0}</Text>
							</View>
						</View>
				}
			</View>
		</View>
	)
}
