import { View, Button } from '@tarojs/components'
import Taro from '@tarojs/taro'
import { useEffect, useState } from 'react'
import './style.scss'
import img from '@/utils/img'

export default props => {
	const { data } = props

	const [ctx, setCtx] = useState(Taro.createInnerAudioContext())
	// 表示是否处理暂停状态
	const [paused, setPaused] = useState(true)

	useEffect(()=>{
		return ()=>{
			// 保证当前小程序内存永远最多只能有一个音频上下文
			ctx.destroy()
		}
	}, [])

	const toggle = ()=> {
		ctx.startTime = 100
		ctx.src = img.baseImg+data.audio_src
		if(paused) {
			ctx.play()
			setPaused(false)
		}else{
			ctx.pause()
			setPaused(true)
		}
		console.log('ctx', ctx)
	}
	const seekHandle = flag => {
		ctx.seek(100)
	}

	return (
		<View className='qf-audio'>
			<View className='qf-audio-wrap'>
				<View className='pp'>播放</View>
			</View>
		</View>
	)
}
