import fetch from './fetch'

// 登录接口
export function fetchLogin(data) {
	return fetch('/user/login', 'post', data||{})
}

// 首页活动列表
export function fetchActList(data) {
	return fetch('/act/list', 'get', data||{})
}

// 获取书城页面的图书列表
export function fetchBookList(data) {
	return fetch('/book/list', 'get', data||{})
}

// 获取听书列表
export function fetchListenList(data) {
	return fetch('/listen/list', 'get', data||{})
}

// 获取图书详情
export function fetchBookDetail(data) {
	return fetch('/book/detail', 'get', data||{})
}

// 点赞接口
export function fetchAddStar(data) {
	return fetch('/publish/star', 'get', data)
}

// 评论接口
export function fetchAddComment(data) {
	return fetch('/publish/comment', 'post', data)
}

// 获取用户信息
export function fetchUserInfo(data) {
	return fetch('/getUserInfo', 'get', data||{})
}

// 更新用户头像和昵称
export function fetchUpdUserInfo(data) {
	return fetch('/updateUserInfo', 'post', data||{})
}

export default {
	fetchLogin,
	fetchActList,
	fetchBookList,
	fetchListenList,
	fetchBookDetail,
	fetchAddStar,
	fetchAddComment,
	fetchUserInfo,
	fetchUpdUserInfo
}
