// 小程序原生scroll-view注意以下几个坑：

// 1、被scroll-view所包裹的子元素，必须添加 id属性，且id属性不能数字开头
// 2、这id是与scroll-view的scroll-into-view属性关联，用于让高亮的元素自动滚入到用户的视野范围内。
// 3、在编写scroll-view时，其样式必须添加上`white-space: nowrap;width: 100%`。

import React, { useState } from 'react'
import {
  Text,
  ScrollView
} from '@tarojs/components'
import './style.scss'

export default props => {

	const {
		data,    // 用于渲染scroll-view的源数据
		label,	 // 用于显示在scroll-view上的字段，默认是源数据中的id
    valueName,   // 中文所对应的英文字段
		value,   // 受控表单的value
		onChange // 用于事件通信，向父组件传递用户选择的结果
	} = props

  const [toView, setToView] = useState(0)

	const itemTap = ele => {
		onChange && onChange(ele)
    setToView(ele.id)
  }

	const renderSCview = ()=> {
    return data.map(ele=>(
      <Text
        className={`item ${value===(ele[valueName||''])?'on':''}`}
        onClick={()=>itemTap(ele)}
        id={'c'+ele.id}
        key={ele.id}>
        {ele[label||'id']}
      </Text>
    ))
  }

	return (
		<ScrollView
			className='qf-scroll-wrap'
			scrollX
			scrollIntoView={'c'+(toView-1)}
		>
			{renderSCview()}
		</ScrollView>
	)
}
