import {
  View,
	Input,
  Image
} from '@tarojs/components'
import img from '../../utils/img'
import './style.scss'

export default props => {

  let {
    disabled,  // 用于控制表单是否禁用，类型是布尔值，默认是false
    value,     // 用于受控表单的绑定，类型是字符串
    onInput,   //用于表单取值
    onConfirm  // 点击小键盘上确定或搜索按钮
  } = props

	return (
		<View className='qf-search'>
			<View className='qf-search-wrap'>
        <Input
          disabled={disabled||false}
          placeholder='搜索'
          value={value||''}
          onInput={e=>onInput&&onInput(e.detail.value)}
          onConfirm={()=>onConfirm&&onConfirm()}
          send='OK'
          done='搜索'
        />
        <Image src={img.search} />
      </View>
		</View>
	)
}
